import Zooming from 'zooming';
const zooming = {
  data() {
    return { vendor: '', currentRoute: 'Allpost' };
  },
  methods: {},
  mounted: function() {
    const zooming = new Zooming({});
    zooming.listen('.post-content p img');
  }
};

export default zooming;



