const UAParser = require('ua-parser-js');
const isApp = {
  data() {
    return { vendor: '', currentRoute: 'Allpost' };
  },
  methods: {
    isAppInit() {
      const parser = new UAParser();
      const { type } = parser.getResult().device;
      this.vendor = type;
      localStorage.setItem('vendor', type);
    },
    getIsApp() {
      return localStorage.getItem('vendor') === 'undefined'
        ? ''
        : localStorage.getItem('vendor');
    },
    handleRoute: function(route = 'home') {
      this.$router.push(route);
    }
  },
  mounted: function() {
    if (!this.getIsApp()) {
      this.isAppInit();
    } else {
      this.vendor = this.getIsApp();
    }
    this.currentRoute = this.$route.name;
  }
};

export default isApp;



