<template>
  <div class="online-resume-container">
    <div class="app-avatar">
      <img src="../assets/avatar-mini.png" alt="avatar">
    </div>
    <div class="app-info-box">
      <div class="top">
        <h1 data-v-5484385f class="username">Dendoink</h1>
      </div>
      <div class="position">
        <span class="content">
          <span>前端开发工程师</span>
          <span>@公众号：前端恶霸</span>
          <span>掘金联合编辑</span>
        </span>
      </div>
      <div class="intro">
        <span data-v-5484385f class="content">常恐不才身，复作无名死</span>
      </div>
    </div>
    <div class="app-person-info">
      <div class="skills personal-info-part">
        <div class="info-header">Contact</div>
        <div class="contact-body">
          <div class="actions">
            <a href="https://github.com/DendiSe7enGitHub">
              <mu-button color="#ff5722">
                <mu-icon value="code"></mu-icon>
                <span>Github</span>
              </mu-button>
            </a>
            <a href="https://juejin.im/user/585a2f52128fe10069ba1b95">
              <mu-button color="purple">
                <mu-icon value="public"></mu-icon>
                <span>Juejim</span>
              </mu-button>
            </a>
            <a href="mailto:DendiSe7en@gmail.com">
              <mu-button color="indigo">
                <mu-icon value="email"></mu-icon>
                <span>DendiSe7en@gmail.com</span>
              </mu-button>
            </a>
          </div>
        </div>
      </div>
      <div class="skills personal-info-part">
        <div class="info-header">Skills</div>
        <mu-container>
          <mu-flex class="demo-linear-progress" v-for="(skill,index) in skills" :key="skill.label">
            <span>{{skill.label}}</span>
            <mu-linear-progress
              mode="determinate"
              :value="skill.percentage"
              :size="10"
              :color="index% 2 ===0 ? 'secondary' : 'green'"
            ></mu-linear-progress>
          </mu-flex>
        </mu-container>
      </div>
      <div class="open-source personal-info-part">
        <div class="info-header">Opensource</div>
        <div class="opensource-list-outter">
          <mu-icon
            value="arrow_back_ios"
            v-show="openSourceList.length > 1"
            @click="change('pre')"
            class="left-icon"
          ></mu-icon>
          <div
            class="opensource-list-wrapper"
            v-for="(opensource,index) in openSourceList"
            :key="opensource.name"
            v-show="index===currentIndex"
          >
            <article class="opensource-item-wrapper home-section-wrapper">
              <a class="opensource-item" :href="opensource.link">
                <div class="opensource-item-header">
                  <a
                    :class="{scaleInVerTop:animate}"
                    :style="`background-image: url(${opensource.img});`"
                  />
                </div>
                <h3 class="opensource-name">{{opensource.name}}</h3>
                <p class="opensource-description">{{opensource.description}}</p>
              </a>
            </article>
          </div>
          <mu-icon
            value="arrow_forward_ios"
            v-show="openSourceList.length > 1"
            @click="change('next')"
            class="right-icon"
          ></mu-icon>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import isApp from "../mixins/isApp";
export default {
  // About
  name: "About",
  mixins: [isApp],
  data() {
    return {
      currentIndex: 0,
      openSourceList: [
        {
          img:
            "http://ww1.sinaimg.cn/large/88b26e1cgy1g0uaebichjj21041ek16z.jpg",
          link: "https://juejin.im/book/5c526902e51d4543805ef35e",
          name: "你不知道的 Chrome 调试技巧",
          description: "熟练掌握 Chrome 调试技巧，直接提升工作效率。"
        },
        {
          img:
            "http://ww1.sinaimg.cn/large/88b26e1cgy1g0u9vc70hdj21f20rmahg.jpg",
          link: "https://github.com/DendiSe7enGitHub/Align",
          name: "Align",
          description: "Real-time, Beautiful blog generator, Auto deploy"
        }
      ],
      skills: [
        { label: "HTML", percentage: 80 },
        { label: "CSS3", percentage: 60 },
        { label: "Javascript", percentage: 60 },
        { label: "React", percentage: 60 },
        { label: "Vue", percentage: 60 },
        { label: "Git", percentage: 70 },
        { label: "Webpack", percentage: 50 }
      ],
      animate: false
    };
  },
  methods: {
    change: function(action) {
      const last = this.openSourceList.length - 1;
      if (this.currentIndex === 0) {
        this.currentIndex = action === "pre" ? last : 1;
      } else if (this.currentIndex === last) {
        this.currentIndex = action === "pre" ? this.currentIndex - 1 : 0;
      } else {
        this.currentIndex =
          action === "pre" ? this.currentIndex - 1 : this.currentIndex + 1;
      }
    }
  }
};
</script>

<style scoped>
.online-resume-container {
  background: #f4f5f5;
  padding-bottom: 65px;
}
.personal-info-part {
  margin-top: 1.5rem;
}
.app-avatar,
.app-info-box,
.app-person-info {
  background: #ffffff;
}
.app-avatar img {
  width: 100px;
  border-radius: 50%;
  margin-top: 20%;
}
.app-info-box {
  padding: 1rem 1rem 2rem 1rem;
  margin-bottom: 0.5rem;
}
.app-info-box .top .username {
  margin: 0;
  padding: 0;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.2;
  color: #000;
}

.position {
  font-size: 1rem;
  line-height: 1.5;
  color: #72777b;
}

.intro {
  font-size: 1rem;
  line-height: 1.5;
  color: #72777b;
}
.actions {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 12rem;
}
.actions a {
  width: 100%;
}
.actions a button {
  width: 100%;
}
.online-resume-container {
  min-height: 100vh;
}

.app-person-info {
  padding: 1rem;
}

.app-person-info .container .demo-linear-progress {
  justify-content: center !important;
  align-items: center !important;
}

.app-person-info .container .demo-linear-progress span {
  margin-right: 5px;
  font-weight: 100;
  font-size: 1rem;
  width: 7rem;
}
.opensource-list-outter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 3rem;
}
.opensource-list-wrapper {
  transition: all 0.5s ease-in-out;
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 7px 19px 3px #949494;
}
.opensource-list-wrapper .opensource-item-wrapper {
  width: 100%;
  border: 1px solid #ccc;
  padding: 1rem;
  border-radius: calc(0.25rem - 1px);
}

.opensource-item-wrapper .opensource-item-header {
  background: #f05a5a;
}
.opensource-item-wrapper .opensource-item {
  background-image: url(https://marketplace-images.githubusercontent.com/703/8af94adc-e40e-11e7-8871-55e85b471458?auto=webp&format=jpeg&width=600);
  background-size: cover;
  background-position: center top;
  color: #585858;
}
.opensource-item-header a {
  background-position: 50% 50% !important;
  border: 1px solid rgba(0, 0, 0, 0.15) !important;
  background-size: cover !important;
  background-origin: border-box !important;
  height: 250px !important;
  display: block !important;
}

.opensource-item-wrapper:hover {
  -webkit-animation: wobble-hor-bottom 0.8s both;
  animation: wobble-hor-bottom 0.8s both;
}
.opensource-item .opensource-description,
.opensource-item .opensource-name {
  margin-top: 4px;
}
.opensource-item .opensource-description {
  font-size: 0.8rem;
}
.opensource-item .opensource-name {
  font-size: 1rem;
}

.info-header {
  padding-top: 4px;
  font-size: 1.3rem;
  font-size: 1.2rem;
  text-align: left;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #eeeeee;
  margin-bottom: 1rem;
}
.right-icon {
  margin-left: 1rem !important;
}
.left-icon {
  margin-right: 1rem !important;
}
.scaleInVerTop {
  -webkit-animation: scale-in-ver-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
  animation: scale-in-ver-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
@-webkit-keyframes scaleInVerTop {
  0% {
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
    -webkit-transform-origin: 100% 0%;
    transform-origin: 100% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
    -webkit-transform-origin: 100% 0%;
    transform-origin: 100% 0%;
    opacity: 1;
  }
}
@keyframes scaleInVerTop {
  0% {
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
    -webkit-transform-origin: 100% 0%;
    transform-origin: 100% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
    -webkit-transform-origin: 100% 0%;
    transform-origin: 100% 0%;
    opacity: 1;
  }
}
</style>



