<template>
  <div class="home_post_list">
    <div class="post-list-wrapper" v-show="!vendor">
      <PostList class="row1" rowsNumber="1"/>
      <PostList class="row2" rowsNumber="2"/>
      <PostList class="row3" rowsNumber="3"/>
    </div>
    <div class="app_post_list" v-show="vendor">
      <div class="app-post-item">
        <section
          v-for="(post,index) in allPosts"
          :key="post.name"
          class="app-post-container"
          @click="handlePostLink(index)"
        >
          <div class="app-post-img-container">
            <a
              href="#"
              :style="`background-image: url(${zipedCovers[zipedCovers.length -1 -index]});`"
            />
          </div>
          <div class="app-post-content-container">
            <div class="app-post-tags">
              <mu-badge
                v-for="(tag,index) in post.tags"
                :key="`${tag}`"
                :content="tag"
                :color="index%2 === 0 ? 'primary' :'secondary'"
              ></mu-badge>
            </div>
            <h3>{{post.title}}</h3>
            <div class="app-post-description">{{post.info}}</div>
            <div class="app-post-footer">
              <div class="app-post-avatar">
                <img src="../assets/avatar-mini.png" alt="avatar">
              </div>
              <div class="app-post-info">
                <span class="date">{{post.date | moment}}</span>
                <span class="read-time">{{Math.floor(post.count/400)}} mins to read</span>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import isApp from "../mixins/isApp";
import { postData } from "../utils/data.js";
import moment from "moment";
import { getAllCategories } from "../utils/datafilter.js";
import { zipedCovers } from "../utils/zipcover.js";
export default {
  // 主页
  name: "Allpost",
  mixins: [isApp],
  data() {
    return {
      allPosts: JSON.parse(postData).reverse(),
      categories: getAllCategories(JSON.parse(postData)),
      open: false,
      zipedCovers: zipedCovers
    };
  },
  components: {
    PostList: () => import("../components/PostList")
  },
  mounted() {},
  filters: {
    moment: function(date) {
      return moment(date).format("MMMM Do YYYY");
    }
  },
  methods: {
    closeBottomSheet() {
      this.open = false;
    },
    handlePostLink: function(index, dir = "post") {
      const appCurrentPost = this.allPosts[index];
      window.localStorage.setItem(
        "appCurrentPost",
        JSON.stringify(appCurrentPost)
      );
      let postName = appCurrentPost.name;
      this.$router.push(`/${dir}/${postName}`);
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 800px;
  width: 800px;
  margin: 0 auto;
  position: relative;
}
.home_post_list {
  display: flex;
  flex-direction: column;
}
.post-list-wrapper {
  display: flex;
}

@media screen and (max-width: 1000px) and (min-width: 300px) {
  .home_post_list {
    display: flex;
    flex-direction: column;
  }
}

.app_post_list {
  min-height: 100vh;
}

.app_post_list .app-post-item {
  margin-top: 36px;
  padding: 1.2rem;
}

.app-post-item section {
  padding-bottom: 2rem;
  border-bottom: 1px solid #ededed;
  margin-bottom: 2rem;
}
.app-post-container .app-post-img-container a {
  background-image: url(https://cdn-images-1.medium.com/max/1200/1*sezckhAOtvMcyWvBUjk5eA.jpeg);
  background-position: 50% 50% !important;
  border: 1px solid rgba(0, 0, 0, 0.15) !important;
  background-size: cover !important;
  background-origin: border-box !important;
  height: 170px !important;
  display: block !important;
}

.app-post-content-container h3 {
  line-height: 1.1;
  text-align: left;
  padding: 0rem 0rem 1rem 0.3rem;
  font-size: 1.4rem;
  font-weight: 600;
}
.app-post-description {
  line-height: 1.2 !important;
  color: rgba(0, 0, 0, 0.54) !important;
  fill: rgba(0, 0, 0, 0.54) !important;
  font-size: 1.1rem;
  text-align: left;
  font-weight: 300;
  padding: 0rem 0rem 1rem 0.3rem;
}

.app-post-footer {
  display: flex;
}

.app-post-footer .app-post-avatar {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-post-footer .app-post-avatar img {
  padding: 3px;
  width: 100%;
  border-radius: 50%;
  border: 1px solid #53af77;
}

.app-post-footer .app-post-info {
  display: flex;
  flex-direction: column;
  padding-left: 1rem;
  color: rgba(0, 0, 0, 0.54) !important;
  fill: rgba(0, 0, 0, 0.54) !important;
  font-size: 0.5rem;
  justify-content: center;
  min-width: 30%;
  font-size: 1rem;
}

.app-post-footer .app-post-info .date {
  text-align: left;
}

.app-post-footer .app-post-info .read-time {
  text-align: left;
}
.app-post-tags {
  display: flex;
  padding-bottom: 1rem;
  padding-top: 1rem;
}
.app-post-tags .mu-badge-container {
  margin: 2px !important;
}
</style>



