<template>
  <div>
    <div v-show="!vendor" class="gallery-container">
      <div class="gallery-header">
        <div class="logo">
          <span class="name">Dendoink</span>
          <span class="text">Photography</span>
        </div>
      </div>
      <div class="gallery-photo-container">
        <div class="photo-list">
          <div class="single-photo-parent" v-for="(item,index) in firstColumnPhotos" :key="index">
            <img :src="item.url" :alt="item.description" class="img-responsive">
            <div class="photo-info">
              <span
                v-for="(tag,index)
              in
              item.tags"
                :key="`${tag}-${index}`"
              >{{tag}}</span>
            </div>
          </div>
        </div>
        <div class="photo-list">
          <div class="single-photo-parent" v-for="(item,index) in secondColumnPhotos" :key="index">
            <img :src="item.url" :alt="item.description" class="img-responsive">
            <div class="photo-info">
              <span
                v-for="(tag,index)
              in
              item.tags"
                :key="`${tag}-${index}`"
              >{{tag}}</span>
            </div>
          </div>
        </div>
        <div class="photo-list">
          <div class="single-photo-parent" v-for="(item,index) in thirdColumnPhotos" :key="index">
            <img :src="item.url" :alt="item.description" class="img-responsive">
            <div class="photo-info">
              <span
                v-for="(tag,index)
              in
              item.tags"
                :key="`${tag}-${index}`"
              >{{tag}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="app-gallery-list-wrapper" v-show="vendor">
      <mu-container>
        <mu-flex justify-content="center">
          <mu-grid-list class="gallery-list">
            <mu-grid-tile
              v-for="(img,index) in appImgList"
              :key="'tile' + index"
              title-position="bottom"
              action-position="left"
              :rows="img.featured ? 2 : 1"
              :cols="img.featured ? 2 : 1"
            >
              <img class="auto_img" :src="img.url">
              <span slot="title">{{img.title}}</span>
              <span slot="subTitle">{{img.tags}}</span>
            </mu-grid-tile>
          </mu-grid-list>
        </mu-flex>
      </mu-container>
    </div>
  </div>
</template>

<script>
import isApp from "../mixins/isApp";
import zooming from "../mixins/zooming";
import moment from "moment";
import { galleryData } from "../utils/galleryData";
export default {
  // 主页
  name: "Gallery",
  mixins: [isApp, zooming],
  props: {},
  data() {
    return {
      firstColumnPhotos: [],
      secondColumnPhotos: [],
      thirdColumnPhotos: [],
      galleryData: galleryData,
      appImgList: []
    };
  },
  computed: {},
  methods: {
    handlePostLink: function(index, dir = "post") {
      window.localStorage.setItem(
        "currentPosts",
        JSON.stringify(this.currentPosts)
      );
      let postName = this.currentPosts[index].name;
      this.$router.push(`/${dir}/${postName}`);
    }
  },
  filters: {
    moment: function(date) {
      return moment(date).format("MMMM Do YYYY");
    },
    daysAgo: function(date) {
      let days = new Date() - new Date(date);
      return parseInt(days / (1000 * 60 * 60 * 24));
    }
  },
  mounted() {
    if (this.galleryData) {
      if (this.vendor) {
        this.appImgList = this.galleryData.map((item, index) => {
          return {
            url: item.mobileUrl,
            date: item.date,
            description: item.description,
            tags: item.tags.join(` · `),
            title: item.title,
            featured: index % 3 === 0
          };
        });
      } else {
        for (let x = 0; x < this.galleryData.length; x += 3) {
          if (this.galleryData[x]) {
            this.firstColumnPhotos.push(this.galleryData[x]);
          }
          if (this.galleryData[x + 1]) {
            this.secondColumnPhotos.push(this.galleryData[x + 1]);
          }
          if (this.galleryData[x + 2]) {
            this.thirdColumnPhotos.push(this.galleryData[x + 2]);
          }
        }
      }
    }
  }
};
</script>

<style scoped>
.gallery-container {
  margin-top: 3rem;
  padding: 2rem 3rem;
}
.gallery-header {
  margin-bottom: 2rem;
}
.gallery-header .logo {
  color: #000000;
}
.gallery-photo-container {
  display: flex;
}
.gallery-header .logo .name {
  display: block;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 0.9rem;
  letter-spacing: 2px;
}
.gallery-header .logo .text {
  display: block;
  text-transform: uppercase;
  font-size: 0.55rem;
  letter-spacing: 8.6px;
  opacity: 0.3;
}
.photo-list {
  max-width: 33%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.photo-list .single-photo-parent {
  display: inline-block;
  margin-bottom: 1rem;
  transition: all 0.2s ease-in-out;
  border-bottom-right-radius: calc(0.25rem - 1px);
  border-bottom-left-radius: calc(0.25rem - 1px);
}
.single-photo-parent img {
  height: auto;
  max-width: 100%;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-top-right-radius: calc(0.25rem - 1px);
  border-top-left-radius: calc(0.25rem - 1px);
}

.single-photo-parent:hover {
  -webkit-box-shadow: 0 18px 32px -18px #000;
  box-shadow: 0 18px 32px -18px #000;
  -webkit-transform: translateY(-6px);
  transform: translateY(-6px);
}
.single-photo-parent .photo-info {
  border-bottom-right-radius: calc(0.25rem - 1px);
  border-bottom-left-radius: calc(0.25rem - 1px);
  border: 1px solid #cccccc75;
  padding: 1rem 2rem;
  display: flex;
  opacity: 0;
  justify-content: space-around;
  color: #cccccc;
}

.single-photo-parent:hover .photo-info {
  display: flex;
  opacity: 1;
  cursor: pointer;
}
.photo-info span {
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
  border-bottom: 1px solid #ffffff;
  font-size: 0.8rem;
}
.single-photo-parent:hover span {
  border-bottom-color: #cccccc;
  color: #8d8d8d;
}
.gallery-list {
  margin: 0 !important;
}
.app-gallery-list-wrapper .container {
  padding: 0px !important;
  width: 100% !important;
}

.gallery-list .mu-grid-tile-wrapper .mu-grid-tile {
  position: relative;
  height: 100%;
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery-list .mu-grid-tile-wrapper .mu-grid-tile > img {
  height: 100%;
  position: relative;
  width: 100%;
}
.mu-grid-tile-subtitle {
  padding-top: 4px !important;
}
</style>



